import { createProducts } from "../../reducers/productReducer";
import { useState } from "react"
import { useDispatch } from "react-redux"
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import {notice} from "../../reducers/notificationReducer";
import { useSelector } from "react-redux";

function NewProduct(){
    const dispatch = useDispatch()
    const [categoria,setCategoria] = useState("")
    const [hinta,setHinta] = useState("")
    const [tuote,setTuote] = useState("")
    const [kuva,setKuva] = useState("")
    const [kuvaus,setKuvaus] = useState("")
    const [määrä,setMäärä] = useState("")
    const user = useSelector(state => state.currentUser)
    const newprod = (event) => {
        event.preventDefault()
        const productObj = ({
        categoria: categoria,
      hinta: hinta,
      tuote: tuote,
      kuva: kuva,
      kuvaus: kuvaus,
      määrä: määrä
        })
        dispatch(createProducts(productObj))
    setCategoria("")
    setHinta("")
    setTuote("")
    setKuva("")
    setKuvaus("")
    setMäärä("")
    dispatch(notice("lisäsit uuden tuotteen",3))
    }

if(user){
    return (<div className="container" style={{height:"100vh"}}>
        <Form onSubmit={newprod}>
          <Form.Group>
           <Form.Control id="categoria"
              value={categoria}
              required
              onChange={(event) => setCategoria(event.target.value)}
              type="text"
              placeholder="categoria"></Form.Control>
            <Form.Control  id="tuote"
              value={tuote}
              onChange={(event) => setTuote(event.target.value)}
              type="text"
              required
              placeholder="tuote"></Form.Control>
            <Form.Control  id="kuvaus"
              value={kuvaus}
              onChange={(event) => setKuvaus(event.target.value)}
              type="text"
              required
              placeholder="kuvaus"></Form.Control>
            <Form.Control  id="hinta"
              value={hinta}
              required
              onChange={(event) => setHinta(event.target.value)}
              type="number"
              placeholder="hinta"></Form.Control>
        <Form.Control  id="määrä"
              value={määrä}
              required
              onChange={(event) => setMäärä(event.target.value)}
              type="number"
              placeholder="määrä"></Form.Control>
               <Form.Control  id="kuva"
              value={kuva}
              required
              onChange={(event) => setKuva(event.target.value)}
              type="text"
              placeholder="kuva linkki"></Form.Control>

          <Button id="addProduct" type="submit" style={{margin: "5px"}}>
             Lisää tuote
            </Button> 
          </Form.Group>
        </Form>
        </div>
    )
}
    else
{
    return(
    <>
    <h2 style={{color:"white"}}>Kirjaudu sisään ensin</h2>
    </>
    )
}

}

export default NewProduct